p>span{display:block;color:#f5f5f5}p>span:nth-child(1){font-size:18px;color:var(--primary-color);font-weight:200}p>span:nth-child(1) span{display:inline;font-weight:100;font-style:italic}p>span:nth-child(2){font-size:14px}p>span:nth-child(3){font-size:18px}p>span:nth-child(3) span{display:inline-block}p>span:nth-child(3) span::first-letter{color:var(--primary-color)}

.searchbox-container{width:100%;position:relative}.searchbox-container .search-loader{display:inline-block;height:11vmin;position:absolute;top:50%;right:0;transform:translateY(-50%)}.searchbox-container .search-loader svg{height:11vmin;width:auto}.searchbox{width:100%;height:12vmin;border-radius:.5em;font-size:4vmin;padding:0 6vmin;outline:none;border:1px solid #e0e0e0;box-shadow:0 5px 5px rgba(0,0,0,0.25),inset 0 5px 5px rgba(0,0,0,0.25);transition:all 100ms}.searchbox::-webkit-input-placeholder{text-align:center;color:#e5e5e5}.searchbox:-ms-input-placeholder{text-align:center;color:#e5e5e5}.searchbox::-ms-input-placeholder{text-align:center;color:#e5e5e5}.searchbox::placeholder{text-align:center;color:#e5e5e5}.searchbox:focus{border:2px #FF7324 solid;transform:scaleX(1.01) scaleY(1.05);box-shadow:0 7px 5px rgba(0,0,0,0.25),inset 0 7px 5px rgba(0,0,0,0.25)}

.home{min-height:100%;display:flex;flex-direction:column;align-items:center;position:relative;overflow:hidden;padding:0 10px}.home .search-error{color:rgba(0,0,0,0.5);font-size:2em}

div.logo{margin:0 0 20px 0;font-family:Lobster Two;font-style:normal;font-weight:normal;font-size:48px;line-height:60px;color:var(--primary-color);align-self:flex-start}div.logo.mob{font-size:24px}

.listItem{display:flex;justify-content:space-between}.listItem__leadimg{width:15vw;height:10vh}.listItem__leadimg__img{max-width:100%;max-height:100%;width:auto;height:auto}.listItem__text{margin:0 0 0 1rem;flex-grow:1;max-width:75%}.listItem__text h1,.listItem__text h3{margin:0;text-align:left}.listItem__text h1{color:var(--primary-color);font-weight:500;letter-spacing:0.07em}.listItem__text h3{color:whitesmoke;font-weight:400;letter-spacing:0.05em;text-transform:capitalize}

.search-suggestion{width:90%;margin:1.5em auto 0 auto;border-radius:.7em}.search-suggestion .list{padding:0}.search-suggestion .list .list__list-item{list-style:none}.search-suggestion .list .list__list-item .listItem{margin:.8em 0;background-color:rgba(0,0,0,0.3);padding:.7em;border-radius:.7em}.search-suggestion .pagination{list-style:none;display:flex;margin:0 auto;justify-content:center;margin-top:2.5em;padding:0}.search-suggestion .pagination>li{height:30px;min-width:30px;background-color:var(--primary-color);border-radius:.5em;border:solid 1px black;display:grid;place-content:center;font-size:.7em;font-weight:bold;padding:.5em;margin:0 .5em;cursor:pointer}.search-suggestion .pagination>li.active-item{box-shadow:0 0 10px var(--primary-color)}.search-suggestion .pagination>li.disabled{-webkit-filter:brightness(0.7);filter:brightness(0.7);pointer-events:none}.search-suggestion .pagination>li>a{display:inline-block;padding:.9em;margin:-.9em;outline:none}

.App{text-align:center;padding:5vh 5vw;min-height:100%;position:relative;max-width:100vw;overflow:hidden}.App .network-notification{position:absolute;bottom:0;left:0;right:0;height:3vh;font-size:0.8em;display:flex;align-items:center;justify-content:center}

.index-container{max-width:60vw;display:flex;justify-content:center;margin:0 auto}.index-container__index{height:30px;width:30px;background-color:var(--primary-color);border-radius:.5em;display:grid;place-content:center;font-weight:bold;font-size:.8em;margin:0 10px}

.details hr{color:#777777;width:100vw;margin:5vw -5vw;border-top:1px}.details__main{display:flex}.details__main__poster{width:50%;background-color:rgba(0,0,0,0.1);display:flex;justify-content:center;align-items:center;max-height:18em}.details__main__poster img{max-width:100%;max-height:100%;width:auto;height:auto}.details__main__info{width:50%;padding-left:1em;text-align:left}.details__main__info h1,.details__main__info h2{margin:0;font-weight:600;letter-spacing:1px}.details__main__info h1{font-size:1.7em;color:var(--primary-color)}.details__main__info h2{text-transform:capitalize;font-size:1.3em;color:#e5e5e5;line-height:1.5;margin-top:.3em}.details__main__info__sub1{margin-top:.2em;color:#777777}.details__main__info__sub1 .rated{display:inline-block;min-width:2em;text-align:center;padding:.1em;border-color:inherit;border:1px solid;border-radius:3px;color:inherit;margin-right:.7em}.details__main__info__sub1 .duration{color:inherit;white-space:nowrap}.details__main__info__sub2{margin-top:1em;color:whitesmoke;font-weight:300}.details__main__info__sub2 .language{margin-top:.7em;font-style:italic}.details__main__info__sub2 .genre{display:flex;flex-wrap:wrap;margin-top:1em}.details__ratings{display:flex}.details__ratings__rating{width:33%;height:3em;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center;color:var(--primary-color)}.details__ratings__division{width:1px;border-left:1px solid var(--primary-color)}.details__crew{text-align:left;font-size:1.25em;font-weight:300;color:whitesmoke}.details__crew article{margin-top:5vw}.details__crew article span:nth-child(1){color:var(--primary-color);margin-right:10px}

